import { Component, EventEmitter, Prop, Event, h, State } from '@stencil/core';
import { iFormField } from '../../models/form-field.model';

@Component({
  tag: 'radio-field',
  styleUrl: 'radio-field.css',
  shadow: false,
  scoped: true,
})
export class RadioField {
  @Prop({ mutable: false }) field: iFormField;
  @Prop() value: any;
  @Prop() isInvalid: boolean;
  @Prop() isDisabled: boolean;
  @Prop() isHidden: boolean;

  @State() isTouched: boolean = false;
  // @State() value: any;

  @Event({ bubbles: true, composed: true }) valueChanged: EventEmitter<{ key: string; value: any }>;
  @Event({ bubbles: true, composed: true }) fieldBlurred: EventEmitter<{ key: string; }>;

  connectedCallback() {
    // this.value = this.field.value;
  }

  private onValueChange(value: any) {
    // const value = (e.target as HTMLInputElement).value;
    // this.value = value;
    this.valueChanged.emit({ key: this.field.key, value });
  }

  private handleBlur() {
    this.isTouched = true;
    this.fieldBlurred.emit({ key: this.field.key });
  }

  render() {
    if (this.isHidden) {
      return null;
    }

    return (
      <section class="radio-options-container">
        {this.field.options?.map((option) => (
          <section class={`radio-col-span-${this.field.optionSize || 1} radio-field-section`}>
            <label>
              <input
                type="radio"
                hidden
                id={`radio-option-${option[this.field.optionValue]}`}
                name={this.field.key}
                value={option[this.field.optionValue]}
                checked={this.value === option[this.field.optionValue]}
                disabled={this.isDisabled}
                onChange={() => this.onValueChange(option[this.field.optionValue])}
                class={`${this.value === option[this.field.optionValue] ? 'checked' : ''}`}
                onBlur={this.handleBlur.bind(this)}
              />

              <span></span>
            </label>

            <div class={`${this.value === option[this.field.optionValue] ? 'checked' : ''} ${this.isTouched ? 'touched' : ''} ${this.isInvalid ? 'invalid' : ''} checkbox-field-content`}>
              {option[this.field.optionLabel] && (
                <label
                  htmlFor={`radio-option-${option[this.field.optionValue]}`}
                  innerHTML={option[this.field.optionLabel]}></label>
              )}

              {option[this.field.optionDescription] && (
                <p innerHTML={option[this.field.optionDescription]}></p>
              )}
            </div>
          </section>
        ))}

        <slot></slot>
      </section>
    );
  }
}
